import React, { ReactElement } from 'react';
import styled from 'styled-components';
import { motion } from 'framer-motion';
import Paper from '@mui/material/Paper';
import { SectionTitle } from './CommonParts';

// AboutSectionProps
interface AboutSectionProps {}

const SectionWrapper = styled(motion.section)`
  padding: 80px 20px;
  background-color: #fff;
  text-align: center;
  max-width: 900px;
  margin: 0 auto;
  border-bottom: 1px solid #eee;
`;

const Paragraph = styled(motion.div)`
  font-size: 1.1em;
  line-height: 1.8;
  margin-bottom: 20px;
  text-align: left;
`;

function AboutSection(props: AboutSectionProps): ReactElement {
  const sectionVariants = {
    hidden: { opacity: 0, y: 50 },
    visible: {
      opacity: 1,
      y: 0,
      transition: {
        duration: 0.8,
        when: "beforeChildren",
        staggerChildren: 0.2
      }
    },
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: { opacity: 1, y: 0 }
  };

  return (
    <SectionWrapper
      initial="hidden"
      whileInView="visible"
      viewport={{ once: true, amount: 0.3 }}
      variants={sectionVariants}
    >
      <SectionTitle variants={itemVariants}></SectionTitle>
      <Paragraph variants={itemVariants}>
          <span style={{fontSize: 28}}><b>20192022</b></span>
          <Paper sx={{padding: 2}} elevation={3}>
            <div><p style={{textAlign: 'start',fontSize: 20}}><b>  Web</b></p></div>
            <br/>PG
            <br/>ASP .NET / C# / Azure 
            <br/>30
            <br/>
          </Paper>
          <br/>
          <Paper sx={{padding: 2}} elevation={3}>
            <div><p style={{textAlign: 'start',fontSize: 20}}><b>  </b></p></div>
            <br/>PG
            <br/>Vue.js / C# 
            <br/>20
          </Paper>
          <br/>
          <Paper sx={{padding: 2}} elevation={3}>
            <div><p style={{textAlign: 'start',fontSize: 20}}><b>  </b></p></div>
            <br/>PG
            <br/>ASP .NET / C#
            <br/>
          </Paper>
          <br/>
          <span style={{fontSize: 28}}><b>2022</b></span>
          <Paper sx={{padding: 2}} elevation={3}>
            <div><p style={{textAlign: 'start',fontSize: 20}}><b>  Web</b></p></div>
            <br/>
            <br/>React / C# / Java / shell / AWS / Oracle DB / PL/SQL
            <br/>
          </Paper>
          <br/>
      </Paragraph>
    </SectionWrapper>
  );
}

export default AboutSection;