import React, { ReactElement } from 'react';
import styled from 'styled-components';
import { motion } from 'framer-motion';
import Paper from '@mui/material/Paper';
import { Image, YoutubeEmbed } from './CommonParts';
import { SubmitButton } from './CommonParts';
import Portfolio2 from '../media/Portfolio2.png';
import Portfolio4 from '../media/Portfolio4.png';
import { SectionTitle } from './CommonParts';
// PortfolioSectionProps
interface PortfolioSectionProps {}

const SectionWrapper = styled(motion.section)`
  padding: 80px 20px;
  background-color: #fff;
  text-align: center;
  max-width: 900px;
  margin: 0 auto;
  border-bottom: 1px solid #eee;
`;

const Paragraph = styled(motion.div)`
  font-size: 1.1em;
  line-height: 1.8;
  margin-bottom: 20px;
  text-align: left;
`;

function PortfolioSection(props: PortfolioSectionProps): ReactElement {
  const sectionVariants = {
    hidden: { opacity: 0, y: 50 },
    visible: {
      opacity: 1,
      y: 0,
      transition: {
        duration: 0.8,
        when: "beforeChildren",
        staggerChildren: 0.2
      }
    },
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: { opacity: 1, y: 0 }
  };

  return (
    <SectionWrapper
      initial="hidden"
      whileInView="visible"
      viewport={{ once: true, amount: 0.3 }}
      variants={sectionVariants}
    >
      <SectionTitle variants={itemVariants}></SectionTitle>
      <Paragraph variants={itemVariants}>
          <Paper sx={{padding: 2}} elevation={3}>
            <div><p style={{textAlign: 'start',fontSize: 20}}><b>GitHub</b></p></div>
            <br/>
            <form onSubmit={(event) =>{ 
              // 
              event.preventDefault();
              window.open("https://github.com/miyoshiakira", "_blank");
              }}>
                    <SubmitButton type="submit" whileHover={{ scale: 1.05 }} whileTap={{ scale: 0.95 }}>
                      GitHub 
                    </SubmitButton>
            </form>
          </Paper>
          <br/>
          <Paper sx={{padding: 2}} elevation={3}>
            <div><p style={{textAlign: 'start',fontSize: 20}}><b> </b></p></div>
            <br/>
            <form onSubmit={(event) =>{ 
              // 
              event.preventDefault();
              window.open("https://inventorysystem-78695.bubbleapps.io/version-test", "_blank");
              }}>
                  <Paper elevation={3}><Image src={Portfolio2} /></Paper>
                  <br/>
                    <p style={{textAlign: 'start',fontSize: 14}}>test@gmail.com</p>
                    <p style={{textAlign: 'start',fontSize: 14}}>test</p>
                  <br/>
                  <SubmitButton type="submit" whileHover={{ scale: 1.05 }} whileTap={{ scale: 0.95 }}>
                     
                  </SubmitButton>
                  <br/>
            </form>
          </Paper>
          <br/>
          <Paper sx={{padding: 2}} elevation={3}>
            <div><p style={{textAlign: 'start',fontSize: 20}}><b> Web</b></p></div>
            <br/>
            <form onSubmit={(event) =>{ 
              // 
              event.preventDefault();
              window.open("https://miyoshiakira.github.io/word-test-front", "_blank");
              }}>
                  <Paper elevation={3}><Image src={Portfolio4} /></Paper>
                  <br/>
                  <SubmitButton type="submit" whileHover={{ scale: 1.05 }} whileTap={{ scale: 0.95 }}>
                     
                  </SubmitButton>
                  <br/>
            </form>
          </Paper>
          <br/>
          <Paper sx={{padding: 2}} elevation={3}>
            <div><p style={{textAlign: 'start',fontSize: 20}}><b> InfiniteDungeon</b></p></div>
            <br/>
            <Paper elevation={3}>
              <YoutubeEmbed
              src={`https://www.youtube.com/embed/yHEGVRFPii0?si=yxoQTykCEFzLSwUX`} // YouTubeURL
              title="YouTube video player"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
              allowFullScreen
              />
            </Paper>
          </Paper>
          <br/>
      </Paragraph>
    </SectionWrapper>
  );
}

export default PortfolioSection;